/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  height: 90px;
  width: 90px;
  font-size: 0;
  color: transparent;
  background: rgba(217, 222, 218, 0.85);
  border: none;
